// 统一配置
const colorArray = ['#7ac6ff', '#9fd96b', '#7c72d8', '#f1d675', '#9fe0f5', '#43b596', '#4b47a6'];

const itemStyle = {
  normal: {
    // borderColor: '#62b4f2',
    borderWidth: 0,
    borderType: 'solid',
    barBorderRadius: [20, 20, 20, 20],
  }
};

const axisLine = {
  lineStyle: {
    color: '#999'
  }
};

const splitLine = {
  show: true,
  lineStyle: {
    color: '#eee',
    // type: 'dashed',
  }
};

// 柱状统计图
export const BarOptions = (data) => ({
  color: colorArray,
  tooltip: {
    trigger: 'axis',
    axisPointer: {   // 坐标轴指示器，坐标轴触发有效
      type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
    },
    formatter: function(params) {
      let result = ''; // `<div>${params[0].name}订单</div>`;
      params.forEach(function(item) {
        if (item.data === null) {
          result += '';
        } else {
          result += `<div style="padding:0 5px;">
                        <span>${item.data}</span>
                    </div>`;  // <span>${item.seriesName}：</span>
        }
      });
      return result;
    }
  },
  // legend: {
  //   data: ['A', 'B', 'C', 'D', 'E']
  // },
  grid: {
    left: '3%',
    right: '4%',
    bottom: '3%',
    containLabel: true
  },
  xAxis: [{
    type: 'category',
    data: ['A', 'B', 'C', 'D', 'E'],
    silent: true,
    axisLabel: { show: true },
    axisTick: { show: true },
    axisLine: axisLine,
    // splitLine: {
    //   show: true,
    //   lineStyle: {
    //     color: '#e6e6e6',
    //     type: 'dashed',
    //   }
    // },
    // splitArea: {
    //   show: true,
    //   areaStyle: {
    //     color: ['rgba(250,250,250,0.2)', 'rgba(252,252,252,0.3)']
    //   }
    // },
  }],
  yAxis: [
    {
      type: 'value',
      axisLabel: { show: true },
      axisTick: { show: true },
      axisLine: axisLine,
      splitLine: splitLine,
      // splitArea: {
      //   show: true,
      //   areaStyle: {
      //     color: ['rgba(250,250,250,0.3)', 'rgba(252,252,252,0.4)']
      //   }
      // }
    }
  ],
  series: [
    {
      name: 'A',
      type: 'bar',
      stack: '总量',
      barWidth: '30%',
      itemStyle: itemStyle,
      data: data[0] // [ 25, 0, 0, 0, 0 ],
    },
    {
      name: 'B',
      type: 'bar',
      stack: '总量',
      barWidth: '30%',
      itemStyle: itemStyle,
      data: data[1] // [ 0, 35, 0, 0, 0],
    },
    {
      name: 'C',
      type: 'bar',
      stack: '总量',
      barWidth: '30%',
      itemStyle: itemStyle,
      data: data[2] // [ 0, 0, 35, 0, 0],
    },
    {
      name: 'D',
      type: 'bar',
      stack: '总量',
      barWidth: '30%',
      itemStyle: itemStyle,
      data: data[3] // [ 0, 0, 0, 65, 0],
    },
    {
      name: 'E',
      type: 'bar',
      stack: '总量',
      barWidth: '30%',
      itemStyle: itemStyle,
      data: data[4] // [ 0, 0, 0, 0, 65],
    },
  ]
});

// 折线图-爆品分析
export const LineOptions = (data) => ({
  grid: {
    top: '15%',
    left: '10%',
    right: '10%',
    bottom: '20%',
    containLabel: true
  },
  xAxis:  {
    type: 'category',
    boundaryGap: false,
    name : 'EMG',
    data: data.xAxis, // ['10', '20', '30', '40', '50'],
    axisTick:{
      show:false
    },
    axisLine: {
      lineStyle: {
        color: '#aaa'
      }
    },

  },
  yAxis: {
    type: 'value',
    // axisLabel: {
    //     formatter: '{value} %'
    // },
    name: '百分比',
    nameTextStyle: {
      color: '#999'
    },
      // boundaryGap: ['0%', '-20%'],
    axisLine: {
      lineStyle: {
        color: '#aaa'
      }
    },
    axisTick:{
      show:false
    }
  },
  series: [
    {
      name:'销量累计分布',
      type:'line',
      smooth: true,
      data: data.seriesData, // [0, 50, 80, 90, 95],
      lineStyle: {
        normal: {
          color:'#7ac6ff',
          width: 4,
        }
      },
      itemStyle: {
        normal: {
          color: '#7ac6ff',
        }
      },
      symbolSize: 0
    }
  ]
});



