import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import { routerTransition } from '../../router.animations';

import { CONFIG } from '../../shared/services/config';
import { DataService } from '../../shared/services/data.service';
import { YscDataTables } from 'ysc-pop-component';
import * as echart from './echarts';
import * as moment from 'moment';

@Component({
  selector: 'app-explosive-analysis',
  templateUrl: './explosive-analysis.component.html',
  styleUrls: ['./explosive-analysis.component.scss'],
  animations: [routerTransition()]
})
export class ExplosiveAnalysisComponent implements OnInit {

  //选择框-统计时间
  private dateOptions = [];
  //选择框-爆品清单
  private listOptions = [];
  //选择框-部门
  private deptOptions = [];

  //显示分页-爆品清单
  isPagination = false;

  //图表-配置
  lineOption = echart.LineOptions({xAxis: [],seriesData: []});

  constructor(private dataService: DataService) { }

  //部门-select-change
  deptChange(){
    this.getWarehouseList();
    // this.yscTable.reload(); //刷新table-爆品清单
    // this.analysisTable.reload();
    this.getChartData(); //销量累计分布
  }

  //统计时间-select-change
  dateChange(){
    this.setColums(this.params.time);
    this.getWarehouseList()
    // this.yscTable.reload(); //刷新table-爆品清单
    // this.analysisTable.reload();
    this.getChartData(); //销量累计分布
  }

  //爆品清单-select-change
  listChange(){
    const result = this.listOptions.filter((item)=>item.businessId == this.params.businessId);

    this.params.distributionNo = result.length > 0 ? result[0].distributionNo : '';
    this.params.erpWarehouseNo = result.length > 0 ? result[0].erpWarehouseNo : '';
    this.yscTable.reload(); //刷新table-爆品清单
  }

  // 爆品清单-表格信息
  private config = {
    url: CONFIG.URL.warehouseData, // CONFIG.URL.sTable,
    columns: [{
      header: 'EMG编号',
      key: 'goodsNo',
      align: 'center',
    }, {
      header: 'EMG名称',
      key: 'goodsName',
      align: 'center',
      render: text => {
        const t = text.goodsName;
        const len = t.length;
        let result = '';
        if(len > 20){
          const arr = t.split('');
          arr.forEach((item, i)=>{
            const num = i+1;
            result = num % 30 == 0 ? result + item + '<br>': result + item; //每隔30个字 强制换行
          });
        }else{
          result = t;
        }
        return result;
      }
    }, {
      header: '商品条码',
      key: 'upc',
      align: 'center',
    }, {
      header: '上月销量',
      key: 'salesLastMonth',
      align: 'center',
    }, {
      header: '上俩月销量',
      key: 'salesLast2Month',
      align: 'center',
    }, {
      header: '上三月销量',
      key: 'salesLast3Month',
      align: 'center',
    }]
  };

  // 爆品清单-表格参数
  private params = {
    time: '',
    erpWarehouseNo: '',
    distributionNo: '',
    dept: '',
      businessId:''
  };

  // 爆品清单-定义表格
  @ViewChild('yscTable')
  yscTable: YscDataTables;

  // 爆品清单-下载
  showDownload(){
    const params = {
      dept : this.params.dept,
      time:this.params.time,
      erpWarehouseNo: this.params.erpWarehouseNo,
      distributionNo: this.params.distributionNo
    }
    /* this.dataService.getJson(CONFIG.URL.download, params).then((data)=>{
      if(data.success){
        console.log('download', data.body);
      }
    }); */
    //http://ysc.jd.com/app/v1/il/hot/download?time=2017-09&erpWarehouseNo=151&distributionNo=10
    window.open('/app/v1/il/hot/download?time='+params.time+'&erpWarehouseNo='+params.erpWarehouseNo+'&distributionNo='+params.distributionNo+'&dept='+this.params.dept);
  }
  
  // 爆品分析-表格信息
  private analysisConfig = {
    url: CONFIG.URL.hotall,
    columns: [{
      header: '仓库',
      key: 'erpWarehouseName',
      align: 'center',
    }, {
      header: '销量占比',
      key: 'salesPercent',
      align: 'center',

    }, {
      header: '累计销量',
      key: 'sales',
      align: 'center',
    }, {
        header: '拐点销量商品个数',
        key: 'hotGoodsNoSum',
        align: 'center',
    }, {
      header: '拐点前商品销量占比',
      key: 'hotSalesPercent',
      align: 'center',

    }, {
      header: '拐点前商品数量占比',
      key: 'hotGoodsNoSumPercent',
      align: 'center',

    }, {
      header: '上月销量商品个数',
      key: 'salesLastMonth',
      align: 'center',
        render: text => {
            let t = text.salesLastMonth;
            if(t == 0){
              return '--';
            }
            return t;
        }
    }, {
      header: '上上月销量商品个数',
      key: 'salesLast2Month',
      align: 'center',
        render: text => {
            let t = text.salesLast2Month;
            if(t == 0){
                return '--';
            }
            return t;
        }
    }]
  };

  // 爆品分析-定义表格
  @ViewChild('analysisTable')
  analysisTable: YscDataTables;

  ngOnInit() {
    //统计时间-select默认选中项
    this.setDate();
    //动态表头
    this.setColums();
    //获取部门
    this.getDept();

  }

  //统计时间-设置年代和月份
  setDate(){
    const startMonth = parseInt(moment().subtract(1, "months").format("MM"));
    const startYear = parseInt(moment().subtract(1, "months").format("YYYY"));
    let result = []; //{ value: '2017-09', label: '2017年9月' }

    for(let i = startMonth; i>0 ; i--){
      const month = i < 10 ? '0'+i : i;
      const value = startYear+'-'+ month;
      const label = startYear+'年'+month+'月';
      result.push({ value, label });
    }
    this.dateOptions = result;
    this.params.time = result[ 0 ].value;
  }

  // 设置表头
  setColums(date = null){
    let date1 = '', date2 = '', date3 = '';
    if(date == null){
      date1 = moment().subtract(1, 'months').format('YYYY年MM月');
      date2 = moment().subtract(2, 'months').format('YYYY年MM月');
      date3 = moment().subtract(3, 'months').format('YYYY年MM月');
    }else{
      date1 = moment(date).format('YYYY年MM月');
      date2 = moment(date).subtract(1, 'months').format('YYYY年MM月');
      date3 = moment(date).subtract(2, 'months').format('YYYY年MM月');
    }
    // 爆品清单
    this.config.columns[3].header = date1 + '销量';
    this.config.columns[4].header = date2 + '销量';
    this.config.columns[5].header = date3 + '销量';

    this.analysisConfig.columns[3].header = date1 + '拐点销量商品个数';
    this.analysisConfig.columns[6].header = date2 + '拐点销量商品个数';
    this.analysisConfig.columns[7].header = date3 + '拐点销量商品个数';
  }

  // 获取部门
  getDept(){
    this.dataService.getJson(CONFIG.URL.dept)
    .then(data =>{
      if(data.success){
        this.deptOptions = data.body;
        this.params.dept = data.body[0].value;
        //爆品清单-获取库房列表
        this.getWarehouseList();
          //销量累计分布
          this.getChartData();
      }
    });
  }

  // 爆品清单-获取库房列表
  getWarehouseList() {
    this.dataService.getJson(CONFIG.URL.warehouseList,{dept: this.params.dept,time:this.params.time})
    .then(deptData => {
      if (deptData.success) {
        this.listOptions = deptData.body.map((item)=>{
          item.erpWarehouseNo = parseInt(item.erpWarehouseNo);
          item.distributionNo = parseInt(item.distributionNo);
          item.businessId = item.businessId;
          return item;
        });
        this.params.erpWarehouseNo = this.listOptions[0].erpWarehouseNo;
        this.params.distributionNo = this.listOptions[0].distributionNo;
          this.params.businessId = this.listOptions[0].businessId;
        // 刷新表格数据
        this.yscTable.reload();
        this.analysisTable.reload();
      }
    });
  }

  //销量累计分布-获取data
  getChartData(){
    this.dataService.getJson(CONFIG.URL.chart,{time:this.params.time,dept: this.params.dept}).then(chartData => {
        if (chartData.success) {
          const data = {
            xAxis: chartData.body.xAxis,
            seriesData: chartData.body.series[0].series,
          }
          this.lineOption = echart.LineOptions(data);
        }
    });
  };

}



