import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { YscComponentModule } from 'ysc-pop-component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ExplosiveAnalysisRoutingModule } from './explosive-analysis.routing.module';
import { ExplosiveAnalysisComponent } from './explosive-analysis.component';

import { AngularEchartsModule } from 'ngx-echarts';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    YscComponentModule,
    ExplosiveAnalysisRoutingModule,
    NgZorroAntdModule.forRoot(),
    AngularEchartsModule
  ],
  declarations: [ExplosiveAnalysisComponent],
  providers: []
})
export class ExplosiveAnalysisModule {


}



