import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ExplosiveAnalysisComponent } from './explosive-analysis.component';

const routes: Routes = [
    { path: '', component: ExplosiveAnalysisComponent }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class ExplosiveAnalysisRoutingModule { }



